.container-landing {
    .name-picture-display {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: center;
        padding-top: 2%;
        gap: 36px;

        padding-left: 4%;
        padding-right: 4%;

        .profile-picture-wrapper {
            // width: 30%;
            aspect-ratio: 1/1;
        
            .profile-picture {
                border-radius: 50%;
        
                width: 100%;
                height: auto;    
                max-width: 50vw;
                max-height: 50vh;
                aspect-ratio: 1/1;
            }
        }
    }
    
    .name-display {
        font-family: 'Dhurjati', sans-serif; 
        font-size: 120pt;
        display: flex;
        flex-direction: column;
        gap: -10vh;
    
        white-space: pre-line;
    
        .big-text {
            font-size: 14vh;
    
            background-color: var(--primary-color) !important;
            background-image: linear-gradient(90deg, var(--accent-color), var(--text-color)) !important;
            background-size: 100%;

            background-clip: text;
            -webkit-background-clip: text;
            -moz-background-clip: text;
            -webkit-text-fill-color: transparent; 
            -moz-text-fill-color: transparent;
        }
        
        .small-text {
            // font-size: 60pt;
            // font-size: calc(2vw + (2vh * 2));
            font-size: 5vh;
            transform: translateY(-10vh);
        }
    
        .smaller-text {
            // font-size: 18pt;
            font-size: 3vh;
            font-family: 'Lato', sans-serif;
            transform: translateY(-10vh);
        }
    }
    
    .button-row {
        padding-left: 10%;
        padding-right: 10%;
        padding-top: 2%;
    
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }
}

.mobile {

    .landing-page-mobile-warning {
        margin-left: 24px;
        margin-right: 24px;
        padding: 12px;
        padding-top: 0px;
        text-align: center;
        color: red;
        font-size: 14pt;
    }

    .button-row {
        flex-direction: column;
        align-items: center;
        gap: 12px;
    }

    .name-picture-display {
        flex-direction: column;
        text-align: center;

        padding-left: 0%;
        padding-right: 0%;

        .name-display {
            padding-left: 5%;
            padding-right: 5%;
            transform: translateY(-10%) !important;

            .big-text {
                font-size: 60pt;
            }

            .small-text {
                font-family: 'Lato', sans-serif;
                transform: translateY(0%);
                font-size: calc(2vw + (1.5vh * 2));
            }
    
            .smaller-text {
                margin-top: 5%;
                transform: translateY(0%);
                font-size: calc(1vw + (1vh * 2));
            }
        }
    }

    .button-row {
        padding-left: 0%;
        padding-right: 0%;
    }
}